import React from "react";
import Media from "components/Media/Media";

const ChatMessage = ({ avatar, title, message }) => {
    return (
        <Media
            avatar={avatar}
            title={
                <span style={chatFontSize}>
                    {title}
                </span>
            }
            body={
                <span>
                    <p style={chatFontSize}>
                        {message}
                    </p>
                </span>
            }
        />
    )
}

const chatFontSize = {
    fontSize: '0.7rem'
}

export default ChatMessage;